
import {
  defineComponent,
  ref,
  reactive,
  computed,
  onMounted,
  nextTick
} from 'vue'
import { ElMessage, ElMessageBox, ElAutocomplete } from 'element-plus'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { ElForm } from 'element-plus'
import { LinkItem, FormModel, Data, Model } from '../product/type/index'
import { activeFlag, unit, qualityStatusList } from '@/constant/option'
export default defineComponent({
  name: 'product',
  components: {
    ElAutocomplete
  },
  setup() {
    const { t } = useI18n()
    const router = useRouter()
    const dialogFormVisible = ref(false)
    const data = ref<Data[]>([])
    const links = ref<LinkItem[]>([])
    const formModel = ref<FormModel>({ data: [] })
    const form = ref<Model>({})
    const querySearch = (queryString: string, cb: any) => {
      const results = queryString
        ? links.value.filter(createFilter(queryString))
        : links.value
      // call callback function to return suggestion objects
      cb(results)
    }
    const formCancel = () => {
      batchFormRef.value?.resetFields()
      dialogFormVisible.value = false
    }
    const saveBatch = () => {
      nextTick(() => {
        console.log(batchFormRef, 'batchFormRef.value')
        batchFormRef.value?.validate((valid) => {
          console.log(valid, 'vaild')
          if (valid) {
            console.log(form.value, 'form.')
            dialogFormVisible.value = true
          }
        })
      })
    }
    const createFilter = (queryString: any) => {
      return (restaurant: any) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        )
      }
    }
    const filters = (row: any, options: any) => {
      let list = options.filter((e: any) => e.value === row)
      return list.length > 0 ? list[0].label : '--'
    }
    const loadAll = () => {
      return [
        {
          sku: 'xbz',
          productName: 'adcvvaa',
          skuAlias: 'assd'
        },
        {
          sku: 'xbz2',
          productName: '2ad2adcvvaa',
          skuAlias: 'assd'
        },

        {
          sku: 'xbz3',
          productName: '3ad2adcvvaa',
          skuAlias: 'assd'
        }
      ]
    }
    const handleSelect = (item: LinkItem) => {
      console.log(item, 'item')
      formModel.value.state = item.sku
      const params: Data = {}
      params.sku = item.sku
      params.productName = item.productName
      params.skuAlias = item.skuAlias
      formModel.value.data.push(params)
    }
    const handleIconClick = (ev: Event) => {
      console.log(ev)
    }
    // 添加批次属性
    const handleAdd = (rowData: any) => {
      dialogFormVisible.value = true
      console.log(rowData, 'data')
    }
    // 删除
    const handleRow = (rowData: any) => {
      // sku
      const index = formModel.value.data.findIndex(
        (e: any) => e.sku === rowData.sku
      )
      console.log(index, 'index1')

      ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
        distinguishCancelAndClose: true,
        confirmButtonText: t('confirm'),
        cancelButtonText: t('cancel'),
        customClass: 'message-box',
        center: true
      })
        .then(() => {
          formModel.value.data.splice(index, 1)
          ElMessage({
            type: 'success',
            message: t('deleteSuccessful')
          })
        })
        .catch((action: any) => {
          ElMessage({
            type: 'info',
            message: action === 'cancel' ? t('deleteFailed') : t('canceled')
          })
        })
    }

    onMounted(() => {
      links.value = loadAll()
      //   console.log(
      //     router.currentRoute.value.query,
      //     'router.currentRoute.value.query'
      //   )
    })

    const activeFlagArray = computed(() => activeFlag)
    const unitArray = computed(() => unit)
    const qualityStatusArray = computed(() => qualityStatusList)
    const formRef = ref<InstanceType<typeof ElForm>>()
    const batchFormRef = ref<InstanceType<typeof ElForm>>()
    const rules = reactive({
      state: [{ required: true, trigger: 'change' }]
    })
    const batchRules = reactive({
      productionDate: [{ required: true, trigger: 'change' }]
    })
    const cancel = () => {
      formRef.value?.resetFields()
      router.push({ path: '/main/Inbound/inboundWarehouse' })
    }
    const submitForm = () => {
      formRef.value?.validate((valid) => {
        console.log(valid, 'vaild')
        if (valid) {
          console.log(formModel.value.data, 'formModel')
          router.push({ path: '/main/Inbound' })
        }
      })
    }
    return {
      formModel,
      rules,
      cancel,
      submitForm,
      formRef,
      activeFlagArray,
      t,
      ElMessage,
      querySearch,
      createFilter,
      handleSelect,
      handleIconClick,
      loadAll,
      filters,
      data,
      handleRow,
      unitArray,
      handleAdd,
      dialogFormVisible,
      form,
      qualityStatusArray,
      formCancel,
      saveBatch,
      batchRules
    }
  }
})
